import React, { Component } from 'react';
import { Route, Link } from 'react-router-dom';
import './App.css';
import Landing from './components/Landing';
import Dry from './components/Dry';
import Thirtyfive from './components/Thirtyfive';
import Illustration from './components/Illustration';
import Intaglio from './components/Intaglio';
import Painting from './components/Painting';
import Video from './components/Video';
import Animation from './components/Animation';
import Writing from './components/Writing';

class App extends Component {
  render() {
    return (
      <div className="App">
      <header>
         <div className="pinkheader">
          <nav className = "appnav">
            <Link to='/'>about</Link>
            <Link to='/dry'>dry</Link>
            <Link to='/35'>35mm</Link>
            <Link to='/illustartions'>illustations</Link>
            <Link to='/intaglio'>intaglio</Link>
            <Link to='/paintings'>paintings</Link>
            <Link to='/video'>video</Link>
            <Link to='/animation'>animation</Link>
            <Link to='/writing'>writing</Link>
           </nav>
          </div>
       </header>
       <main>
         <Route exact path="/" component={Landing} />
         <Route path="/dry" component={Dry} />
          <Route path="/35" component={Thirtyfive} />
          <Route path="/illustartions" component={Illustration} />
          <Route path="/intaglio" component={Intaglio} />
          <Route path="/paintings" component={Painting} />
          <Route path="/video" component={Video} />
          <Route path="/animation" component={Animation} />
          <Route path="/writing" component={Writing} />
        </main>
      </div>
    );
  }
}

export default App;



