import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './video.css';

  const pieces = [
    {
      photo: "https://i.ibb.co/dbggPn4/2015-35mm-ru-01.jpg",
      alt: "2015-35mm-ru-01.jpg",
      title: "one"
    },
    {
      photo: "https://i.ibb.co/xz1Xv3q/2015-35mm-ru-02.jpg",
      alt: "2015-35mm-ru-02.jpg",
      title: "two"
    }
  ]

class Video extends Component {
  render() {
    return (
      pieces.map((e, idx) => 
        <div>
          <img src={e.photo} alt={e.alt}/>
          <p>{e.title}</p>
        </div>
      )        
    )
  };
}

export default Video;


